.carousel-control-next-icon {
    /* add a transparent background color */
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
}

.carousel-control-prev-icon {
    /* add a transparent background color */
    background-color: rgba(0, 0, 0, 0.5);
